import React from 'react'
import Header from './Header'
import Body from './Body'
import Footer from './Footer'



class Container extends React.Component {
constructor() {
	super()
	this.state = {
		nav: "home",
		CartStatus: 0
	}
}

setNav = (event) => {
	this.setState({nav: [event.target.id]})
}
	render() {
		return(
				<div id="cont">
				<Header home={this.state.nav} setNav={this.setNav} CartStatus={this.state.CartStatus}/>
				<Body nav={this.state.nav}/>
				<Footer />
				</div>
		)
	}
}

export default Container