import React from 'react'
import best5g from './best5g.jpg'
import CartObj from './CartObj'
class Cart extends React.Component {
	constructor() {
		super() 
		this.state = {}
	}

	render() {

		const bb = this.props.ct.length > 1 ? 1 : 2;
		const addrr = this.props.addr
		const hh = this.props.Total
		const gt = this.props.delivery ? this.props.delivery : "Nil"
		const ct = this.props.ct.map(prev => <CartObj init={bb} key={this.props.prev.id} ft={prev} remove={this.props.remove} handleVal={this.props.handleVal} val={this.props.val}/>)
		if(this.props.cart) {
				return(
		
		
						<div className="container border border-dark mt-3 rounded" style={{padding: "20px"}}>
						<ul className="breadcrumb">
						<li className="breadcrumb-item h3">My Cart</li></ul>
						<div className="row">
						<div className="col-md-8">
						<div>
						<input type="text" value={this.props.addr} onChange={this.props.handleaddr} placeholder="Delivery Address" className="form-control" required />
						<span className="pt-3 btn btn-sm" style={{cursor: "pointer", color: "red"}} onClick={this.props.Clear}>Clear Cart</span>
						<hr />
						{ct}
						
						</div>
						</div>
		
						<div className="col-md-4" style={{padding: "10px"}}><h2 className="d-inline" style={{background: "none"}}>Total</h2><select className=" custom-select" style={{float: "right", display: "inline", maxWidth: "60%"}} name="delivery" value={this.props.delivery} onChange={this.props.handleDelivery}><option value="Nil">--Delivery Type--</option><option value="$3.65">Standard - {"$3.65"}</option><option value="$8.99">Premium - {"$8.99"}</option></select><hr />
							<h3 className="text-dark" style={{background: "none"}}>Sub-Total: <span className="float-right">{"$" + hh}</span></h3> <br />
							<h3 className="text-dark" style={{background: "none"}}>Delivery: <span className="float-right">{gt}</span></h3> <br />
							<p>Address: {addrr}</p>
							<center>
							<input type="submit" className="bg-success" style={{padding: "10px", width: "60%", background: 'lightgray', margin: "20px", border: "none"}} value="Checkout" onClick={this.props.order}/>
						</center>
						</div>
						</div>
						</div>
		
		
					)}

					else{
						return(

						<div className="container border border-dark mt-3 rounded" style={{padding: "20px"}}>
						<ul className="breadcrumb">
						<li className="breadcrumb-item h3">My Cart</li></ul>
						<div className="row">
						<div className="col-md-8">
						<div>
						<input type="text" value={this.props.addr} onChange={this.props.handleaddr} placeholder="Delivery Address" className="form-control" required />
						<hr />
						<div style={{padding: "30px", textAlign: "center"}}>
						<p>No Data Available</p>
						 </div>
						<hr />
						</div>
						</div>
		
						<div className="col-md-4" style={{padding: "10px"}}><h2 className="d-inline" style={{background: "none"}}>Total</h2><select className=" custom-select" style={{float: "right", display: "inline", maxWidth: "60%"}} name="delivery" value={this.props.delivery} onChange={this.props.handleDelivery}><option value="Nil">--Delivery Type--</option><option value="3.65">Standard - {"$3.65"}</option><option value="8.99">Premium - {"$8.99"}</option></select><hr />
							<h3 className="text-dark" style={{background: "none"}}>Sub-Total: <span className="float-right">{"Nil"}</span></h3> <br />
							<h3 className="text-dark" style={{background: "none"}}>Delivery: <span className="float-right">{gt}</span></h3> <br />
							<p>Address: {addrr}</p>
							<center>
							<input type="submit" className="bg-success" style={{padding: "10px", width: "60%", background: 'lightgray', margin: "20px", border: "none"}} value="Checkout"/>
						</center>
						</div>
						</div>
						</div>
		
						)
					}
	}
}

export default Cart