import React, {Component} from 'react'
import Product from "./Product"
import FeaturedCollection from './FeaturedCollection'
import Cart from './Cart'
import Hero from './Hero'
import CartData from './CartData'
import ecomm1 from './ecomm1.jpg'
import Phones from './Phones'
import Compt from './Compt'
import Gadgets from './Gadgets'

class Body extends Component {
	constructor() {
		super()
		this.state = {cartd: [],
			cart: "", id: 1,
cartTotal: 0,
delivery: "",
addr: "",
data1: FeaturedCollection,
data2: Phones,
data3: Compt,
data4: Gadgets

		}
	}

order = () => {
	if(this.state.delivery && this.state.addr) {
		alert("Order Placed. \nPayment will be made available")
	   this.setState({cartd: [], cart: "", cartTotal: 0, delivery: "", addr: ""})
} else{alert('No Address / Delivery type')}
}		

handleaddr = (event) => {
this.setState({addr: event.target.value})
}

	handleVal = (event) => {
		console.log(event.target.name)
    this.setState({val: this.state.val + parseInt(event.target.value)}) 
	}
	clear = () => {
	const gg = window.confirm("Are you sure?");
if (gg === true) {
   this.setState({cartd: [], cart: "", cartTotal: 0, delivery: "", addr: ""})
} else if(false) {
    return null
}

		
	}


	handleDelivery = (event) => {
const del = event.target.value
		this.setState({delivery: del })
	}
	
	addToCart = (event) => {
		
	var x;
		if(this.props.nav == "home") {
		x = this.state.data1
	}
	else if(this.props.nav == "phones") {
		x = this.state.data2
	}
	else if(this.props.nav == "computers") {
		x = this.state.data3
	}
	else if(this.props.nav == "gadgets") {
	x = this.state.data4
	}


		const newid = event.target.id
		const add = {
			id: newid,
		 name: x[newid].name,
	 img: x[newid].img,
	 price: x[newid].price}
	this.setState(prev => {cartd: this.state.cartd.push(add)})
	this.setState({cart: true, id: this.state.id + 1, cartTotal: this.state.cartTotal + add.price, val: 0})
const numb = this.state.cartd.length + 1
	const word = "Added Succesfully \n" + "Total Items in cart: " + numb
alert(word)


}


	remove = (name) =>{
		this.setState({cartd: [this.state.cartd.filter(prev => prev.name !== name)]})
		console.log(this.state.cartd)
	
	}

	render() {

		const newb = FeaturedCollection.map(prev => <Product ft={prev} addToCart={this.addToCart} key={prev.id} id={prev.id}/>)
		if(this.props.nav == "home") {

		return (
			<section>
			<div>
			<Hero />
		<div className="container-fluid" style={{maxWidth: "100%", padding: "30px"}}>
		<ul className="breadcrumb">
		<li className="breadcrumb-item h3">Featured Collection</li></ul>
		<div className="row">
			
		{newb}


		</div>
		</div>
		</div>
		</section>
)
		}
		else if(this.props.nav == "phones") {
			const pn = Phones.map(prev => <Product ft={prev} addToCart={this.addToCart} key={prev.id} id={prev.id}/>)
			return(
				<div className="container-fluid" style={{maxWidth: "100%", padding: "30px"}}>
		<ul className="breadcrumb">
		<li className="breadcrumb-item h3">Phones</li></ul>
		<div className="row">
		{pn}

		</div>
		</div>
				)
		}

		else if(this.props.nav == "computers") {
			const pn = Compt.map(prev => <Product ft={prev} addToCart={this.addToCart} key={prev.id} id={prev.id}/>)
			return(
				<div className="container-fluid" style={{maxWidth: "100%", padding: "30px"}}>
		<ul className="breadcrumb">
		<li className="breadcrumb-item h3">Computers</li></ul>
		<div className="row">
		{pn}

		</div>
		</div>
				)
		}

		else if(this.props.nav == "gadgets") {
			const pn = Gadgets.map(prev => <Product ft={prev} addToCart={this.addToCart} key={prev.id} id={prev.id}/>)
			return(
				<div className="container-fluid" style={{maxWidth: "100%", padding: "30px"}}>
		<ul className="breadcrumb">
		<li className="breadcrumb-item h3">Gadgets</li></ul>
		<div className="row">
		{pn}

		</div>
		</div>
				)
		}


		else if(this.props.nav == "myCart") {
return(
				<Cart handleVal={this.handleVal} 
				val={this.state.val}
			    handleDelivery={this.handleDelivery} 
				delivery={this.state.delivery} cart={this.state.cart} ct={this.state.cartd} 
				remove={this.remove} Clear={this.clear} Total={this.state.cartTotal}
				addr={this.state.addr} handleaddr={this.handleaddr}
				order={this.order}
				/>


			)
		}
	}
}

export default Body
