import React, {Component} from 'react'
import logo from './logo.png'
class Footer extends Component {

	render() {
		const date = new Date();
		const year = date.getFullYear();
		return(
			<footer className="container-fluid text-center">

			<hr style={{border: "1px solid black", width: "80%"}}/>
			<div className="row">
			<div className="col-md-4">
			<img src={logo} alt="logo" />
			<p className="text-muted">CopyRight &copy; {year}</p>
			</div>

			<div className="col-md-4">
			<form>
			<h2>Contact Us</h2>
			<p>Send us a message regarding our Products and services.</p>
			<input type="text" className="form-control mt-3 mb-3" placeholder="Your Full Name" />
			<input type="text" className="form-control mt-3 mb-3" placeholder="Your Email Address" />
			<textarea className="form-control mt-3 mb-3" placeholder="Your Message" cols="30" rows="10" />
			<input type="submit" className="btn-outline-dark btn mt-3 mb-3" name="" />
			</form>
				</div>
				<div className="col-md-4">
				<h2>Social</h2>

				<i className="fa fa-facebook fa-2x" style={{padding: "10px"}}></i>
				<i className="fa fa-instagram fa-2x" style={{padding: "10px"}}></i>
				<i className="fa fa-youtube fa-2x" style={{padding: "10px"}}></i>
				<br />
				<br />
				<h2>Our Details</h2>
				<p><i className="fa fa-envelope"></i> Ayomideoloyede95@gmail.com </p>
				<p><i className="fa fa-phone"></i> +234 701 926 0952</p>
				<p><i className="fa fa-home"></i> Abuja, Nigeria. </p>

				</div>
				

							</div>
			</footer>
			)
	}
}

export default Footer