import React from 'react'

function CartObj(props) {
	
	return(<div>
			 <div className="d-flex">
				<div className="flex-item" style={{padding: "30px", paddingTop: "50px"}}><img className="img-thumbnail" src={props.ft.img} width="180" height="160" style={{margin:"10px"}}/>
				</div>
				<div className="flex-item pt-3" style={{marginTop: "60px"}}><h2 style={{color: "black", marginBottom: "5px", background: "none"}}>${props.ft.price}

				</h2>
				<p>{props.ft.name}</p></div>
				
				</div><hr /></div>
			
		)
}

export default CartObj