import React, {Component} from 'react'
import logo from './logo.png'

class Header extends Component {

	render() {
		function showMenu() {
			document.getElementById('nav').classList.toggle('d-block')

		}
		return(
			<div className="header">
			<img src={logo} id="logo" alt="logo" />
			<button className="navbtn" onClick={showMenu}>
			<i className="fa fa-bars" id="menu"></i>
			</button>
			<ul id="nav">
			{this.props.home === "home" ? null : <li id="home" onClick={this.props.setNav}>Home</li>}
			<li id="phones" onClick={this.props.setNav}>Phones</li>
			<li id="computers" onClick={this.props.setNav}>Computers</li>
			<li id="gadgets" onClick={this.props.setNav}>Gadgets</li>
			{this.props.home === "myCart"? null: <li id="myCart" onClick={this.props.setNav}>Cart</li>}
			</ul>
			</div>
			)
	}
}

export default Header