import React, {Component} from 'react'
import ProductsData from "./ProductsData"
import ecomm1 from './ecomm1.jpg'

function Product(props) {
	return(
			
			<div className="col-lg-4 mb-4">
			<div className="product" style={{backgroundImage: `url(${props.ft.img})`}}>

			<h2>{props.ft.name}</h2><h4>${props.ft.price}</h4>
			<center>
			<button className="btn btn-dark" name={props.ft.name} price={props.ft.price} img={props.ft.img} onClick={props.addToCart} id={props.ft.id} style={{cursor: "pointer"}}>Add to cart</button>
			</center></div>

			</div>

		)
}

export default Product